import { useState, useEffect } from "react"
import { useParams } from "react-router-dom"
import styled from "styled-components"
import CommentReply from "./CommentComponents/Reply"
import CommentVote from "./CommentComponents/Vote"
import CommentContent from "./CommentComponents/Content"
import CommentNew from "./CommentComponents/New"
import CommentEdit from "./CommentComponents/Edit"

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
`;

const Maindiv = styled.div`
    display: flex;
`;

const Commentwrap = styled.div`
    display: flex;
    font-size: 2rem;
    flex-direction: row;
    border: 0.1rem solid rgb(179, 175, 175);
    border-bottom-right-radius: 5rem;
    margin: 2rem;
    ${({ active }) => active && `border: 0.5rem solid rgb(2, 2, 2);`};
`;

const Commentreplywrap = styled.div`
    display: flex;
    font-size: 2rem;
    width: 100%;
    flex-direction: column;
`;

const Nocomments = styled.div`
    font-size: 3rem;
    margin: 2rem;
`;

const Comment = () => {

    const user = localStorage.getItem("user");
    
    const { id, type } = useParams()

    const post_id = id
    const comment_id = type
  
    const [ commentsinfo, setCommentsinfo ] = useState();
    const [ newcommentinfo, setNewcommentinfo ] = useState({ comment_content: "", post_id: post_id });
    const [ EditedComment, setEditedComment ] = useState({ edit_comment_content: "", comment_id: "" });
    const [ replyComment, setReplyComment ] = useState({ reply_content: "", comment_id: "", post_id: post_id });
    const [ iseditid, setIseditid ] = useState()
    const [ openReplyArray, setOpenReplyArray ] = useState()

    useEffect(() => {
        if (!comment_id) {
            fetch("/api/get_comments", {
                method: "POST",
                mode: "cors",
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({ post_id })    
            })
            .then(response => response.json())
            .then(data => {
                setCommentsinfo(data)
            })
        } else {
            fetch("/api/get_replies", {
                method: "POST",
                mode: "cors",
                headers: {'Content-Type': 'application/json'},
                body: JSON.stringify({ comment_id })    
            })
            .then(data => data.json())
            .then(res => {
                setCommentsinfo(res)
            })
        }
    }, [comment_id])

    function createComment() {
        fetch("/api/create_comment", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(newcommentinfo)    
        })

        setNewcommentinfo(prevInfo => { return { ...prevInfo, comment_content: "" }})
    }   

    function sendEdit() {
        fetch("/api/edit_comment", {
            method: "PUT",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(EditedComment)    
        })
        setIseditid()
        setEditedComment()
    }

    function createReply() {
        fetch("/api/create_reply", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(replyComment)    
        })
        setReplyComment()
        setOpenReplyArray()   
    }

    function deleteComment(e) {
        const comment_id = e.target.name
        fetch("/api/delete_comment", {
            method: "DELETE",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ comment_id })    
        })
    }

    function voteComment(e) {
        const comment_id = e.target.getAttribute("id")
        let islike = e.target.getAttribute("name")
        fetch(`/api/vote_comment`, {
            method: "PUT",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ islike, comment_id })    
        })

        renderVotechange(islike, comment_id)
    }

    function renderVotechange(islike, comment_id) {

        if (islike == "true") islike = true
        else islike = false

        commentsinfo.every(e => {
            if (e.id == comment_id) {
                if (e.liked == islike) {
                    if (e.liked) e.comment_votes -= 1
                    else e.comment_votes += 1

                    e.liked = null
                }
                else {
                    if (e.liked == null && islike) e.comment_votes += 1
                    else if (e.liked == null && !islike) e.comment_votes -= 1
                    
                    if (e.liked != null && islike) e.comment_votes += 2
                    else if (e.liked != null && !islike) e.comment_votes -= 2

                    e.liked = islike
                }

                return false;
            }

            return true
        });
      
        setCommentsinfo([ ...commentsinfo ])
    }

    function copyClip() {
        navigator.clipboard.writeText(window.location.href) 
    }

    function handleInput(e) { 
        setNewcommentinfo(prevInfo => { 
            return { ...prevInfo, comment_content: e.target.value }
    })}

    function handleInputEdit(e) { 
        setEditedComment(prevInfo => { 
            return { ...prevInfo, edit_comment_content: e.target.value }
    })}
    
    function handleInputReply(e) { 
        setReplyComment(prevInfo => { 
            return { ...prevInfo, reply_content: e.target.value }
    })}

    function openReply(e) {
        setOpenReplyArray(e.target.name);
        setReplyComment(prevInfo => { 
            return { ...prevInfo, comment_id: e.target.name }
        })
    }

    function handleEdit(e) {
        setIseditid(e.target.name)
        setEditedComment(prevInfo => { 
            return { ...prevInfo, comment_id: e.target.name }
        }) 
    }

    return (
        <Wrapper>
            <CommentNew 
                createComment={createComment}
                handleInput={handleInput}
                newcommentinfo={newcommentinfo}
            />
            {commentsinfo?.length ? commentsinfo.map((comment => {
                return (
                    <Maindiv key={comment.id}>             
                        {comment.id == iseditid ?
                        <CommentEdit 
                            sendEdit={sendEdit}
                            handleEdit={handleEdit}
                            handleInputEdit={handleInputEdit}
                        />

                        :

                        <Commentreplywrap>
                            <Commentwrap active={comment.id == comment_id}>
                                <CommentVote 
                                    comment={comment}
                                    voteComment={voteComment}
                                />
                                <CommentContent 
                                    comment={comment}
                                    openReply={openReply}
                                    post_id={post_id}
                                    copyClip={copyClip}
                                    handleEdit={comment.username === user && handleEdit}
                                    deleteComment={comment.username === user && deleteComment}
                                />
                            </Commentwrap>
                            {openReplyArray == comment.id &&
                            <CommentReply
                                handleInputReply={handleInputReply}
                                comment_id={comment.id}
                                createReply={createReply}
                                openReply={openReply}
                            />
                            }
                        </Commentreplywrap>              
                        }
                    </Maindiv>
                )
            }))
            :
            <Nocomments>
                Be the first to comment ...
            </Nocomments>
            }
        </Wrapper>
    )
}

export default Comment;
