import { useState, useEffect } from "react"
import { useParams } from "react-router-dom"
import styled from "styled-components";
import PostVote from "./PostComponents/Vote"
import PostContent from "./PostComponents/Content";
import EditPost from "./PostComponents/Edit";

const Wrapper = styled.div`
    display: flex;
    font-size: 4rem;
    word-break: break-word;
    flex-direction: column;
`;

const Post = styled.div`
    flex: 1;
    display: flex;
    margin: 1rem;
    border-bottom: solid 0.4rem rgb(190, 194, 192);
`;

const Loading = styled.div`
    font-size: 3rem;
`;

const Postdetails = () => {

    const loggedInUser = localStorage.getItem("user");

    const { id } = useParams();
    const post_id = id

    const [ isedit, setIsedit ] = useState(false)
    const [ postinfo, setPostinfo ] = useState();
    
    useEffect(() => {
        fetch("/api/post_info", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ post_id })    
        })
        .then(response => response.json())
        .then(data => { 
            setPostinfo(data)
        })
    }, [post_id])

    function sendEdit() {
        fetch("/api/edit_post", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(postinfo)    
        })

        setPostinfo({ ...postinfo })
        setIsedit(false)
    }
 
    function votePost(e) {
        const islike = e.target.getAttribute("name")
        fetch(`/api/vote_post`, {
            method: "PUT",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ post_id, islike })    
        })

        renderVotechange(islike)
    }
    
    function renderVotechange(islike) {

        if (islike == "true") islike = true
        else islike = false

        if (postinfo.liked == islike) {
            if (postinfo.liked) postinfo.post_votes -= 1
            else postinfo.post_votes += 1

            postinfo.liked = null
        }
        else {
            if (postinfo.liked == null && islike) postinfo.post_votes += 1
            else if (postinfo.liked == null && !islike) postinfo.post_votes -= 1
            
            if (postinfo.liked != null && islike) postinfo.post_votes += 2
            else if (postinfo.liked != null && !islike) postinfo.post_votes -= 2

            postinfo.liked = islike
        }
      
        setPostinfo({ ...postinfo })
    }

    function deletePost() {
        fetch(`/api/delete_post`, {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ post_id })    
        })
        window.location.href = "/"
    }

    function copyClip(e) {
        navigator.clipboard.writeText(window.location.href) 
    }

    function handleInput(e) { setPostinfo(prevInfo => {  
        return { ...prevInfo, post_content: e.target.value }
    })}

    function setEdit() { setIsedit(!isedit) }

    return (
        <Wrapper>
 
            {postinfo ? isedit && postinfo ?
            <EditPost 
                postinfo={postinfo}
                setEdit={setEdit}
                sendEdit={sendEdit}
                handleInput={handleInput}
            />

            : 

            <Post>
                <PostVote 
                    votePost={votePost}
                    postinfo={postinfo}
                />
                <PostContent
                    postinfo={postinfo}
                    copyClip={copyClip}
                    deletePost={loggedInUser === postinfo.username && deletePost}
                    setEdit={loggedInUser === postinfo.username && setEdit}
                />
            </Post>

            :

            <Loading>
                LOADING...
            </Loading>
            }  
        </Wrapper>
    )
}

export default Postdetails;
