import Posts from "./PostList.js";
import Createpost from "./Createpost.js";
import PostRender from "./PostPage/PostRender.js";
import Navbar from "./Navbar.js";
import Followlist from "./Followlist";
import UserprofileRender from "./Userprofile/UserprofileRender.js";
import NotFound from "../Errorpage/NotFound.js";
import Chatbox from "./Chatbox.js";
import MobileNavTop from "./Mobile/MobileNavTop"
import MobileNavBottom from "./Mobile/MobileNavBottom"
import Search from "./Search"
import styled from "styled-components"
import { Route, Switch } from "react-router-dom";

const Wrapper = styled.div`
    display: flex;
    min-height: 100vh;
    flex-direction: row;

    @media(max-width: 768px) {
      flex-direction: column;
    }
`;

const Nav = styled.nav`
    max-height: 100vh;
    flex: 1;
    position: sticky;
    top: 0;
    background-color: #4B5C77;
  
    @media(max-width: 768px) {
      display: none;
    }
`;

const Article = styled.article`
    display: flex;
    flex-direction: column;
    flex: 2.5;
    border: rgb(219, 213, 213) solid 0.1rem;
    background: #ffffff;
`;

const Aside = styled.aside`
    flex: 1;
    max-height: 100vh;
    position: sticky;
    display: flex;
    top: 0;
    flex-direction: column;
    background-color: #4B5C77;
    
    @media (max-width: 768px) {
      display: none;
    }
`;

const LoggedIn = () => {

  return (
        <Wrapper>
            <MobileNavTop />
            <Nav>
              <Navbar />
            </Nav>
            <Article>
              <Switch>
                <Route exact path="/"><Posts /></Route>              
                <Route exact path="/createpost"><Createpost /></Route>
                <Route path="/post/:id/:type?" render={(props) => <PostRender {...props} />}/>
                <Route path="/userprofile/:id" render={(props) => <UserprofileRender {...props} />}/>
                <Route path="/userplace"><Followlist /></Route>
                <Route path="/chat"><Chatbox /></Route>
                <Route path="/search"><Search /></Route>
                <Route><NotFound /></Route>
              </Switch>
            </Article>
            <Aside>
              <Followlist />
            </Aside>
            <MobileNavBottom />
        </Wrapper>
  );
};

export default LoggedIn;
