import styled from "styled-components";

const Parent = styled.div`
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
  text-align: center;
  margin-bottom: 10rem;
  
  @media(max-width: 768px) {
    flex-direction: column;
  }
`;

const H1 = styled.h1`
	  font-size: 5rem;
    padding: 3rem;
    padding-bottom: 8rem;
    width: 60%;
    z-index: 1;
`;

const Img = styled.h1`
    width: 40%;
    z-index: 1;
    h2 {
      font-size: 4.5rem;
    }
    h3 {
      font-size: 4rem;
    }

`;

const Baloon = styled.div`
  border-radius: 50%;
  background: linear-gradient(to right top, rgb(232, 234, 237), rgb(209, 216, 227));
  position: absolute;
  height: 20rem;
  width: 20rem;
`;

export const BaloonTop = styled(Baloon)`
  top: 25%;
  right: 25%;
`;

export const BaloonDown = styled(Baloon)`
  top: 65%;
  right: 60%;
`;


const Entrypage = () => {
  return (
    <Parent>
      <BaloonTop />
      <BaloonDown />
	    <H1>Welcome to Kasacko</H1>
      <Img>
        <h2>Want to Join?</h2>
        <h3>Register now!</h3>
      </Img>
    </Parent>
  )
};
  
export default Entrypage;
