import Userprofile from "./UserprofileComponents/Userprofile"
import Usersettings from "./UserprofileComponents/Usersettings"
import Followrequests from "./UserprofileComponents/Followrequests"
import Userposts from "./UserprofileComponents/Userposts"
import UserprofileNavbar from "./UserprofileComponents/UserprofileNavbar"
import Userlikedposts from "./UserprofileComponents/Userlikedposts"
import Userlikedcomments from "./UserprofileComponents/Userlikedcomments"
import { useParams, Route, Switch } from "react-router-dom"
import { useEffect, useState } from "react"
import styled from "styled-components"

const Wrapper = styled.div`
    display: flex;
    flex: 1;
    flex-direction: column;
    font-size: 4rem;
`;

const UserprofileRender = () => {

    const { id } = useParams()

    const user = id
    
    const [ userdata, setUserdata ] = useState()

    useEffect(() => {
        setUserdata()
        fetch("/api/display_userprofile", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ user })    
        })
        .then(res => res.json())
        .then(userdata => setUserdata(userdata))
        .catch(error => console.log(error))
    }, [user])

    return (
        <>
            {userdata && 
            <Wrapper>

                <Userprofile userdata={userdata} user={user}/>

                <UserprofileNavbar userdata={userdata} user={user}/>

                <Switch>
                    <Route exact path="/userprofile/:id" render={(props) => <Userposts {...props} />}/>
                    <Route exact path="/userprofile/:id/likedposts" render={(props) => <Userlikedposts {...props} />}/>
                    <Route exact path="/userprofile/:id/likedcomments" render={(props) => <Userlikedcomments {...props} />}/>
                    <Route path="/userprofile/:id/followrequests" render={(props) => <Followrequests {...props} />}/>
                    <Route path="/userprofile/:id/usersettings" render={(props) => <Usersettings {...props} />}/>
                </Switch>

            </Wrapper>
            }
        </>
    )
}

export default UserprofileRender;
