import Post from "./Post";
import Comment from "./Comment";
import { useParams } from "react-router-dom"

const PostRender = () => {

    const { id, type } = useParams()

    return (
        <>
            <Post post_id={id} />
            <Comment post_id={id} comment_id={type} /> 
        </>
    )
}

export default PostRender;
