import { useState } from "react";
import styled from "styled-components"
import { Link } from "react-router-dom";

const Loginform = styled.form`
  margin: 2rem;
  display: flex;
  flex-direction: column;
  flex: 1;
  align-items: center;
  justify-content: center;
  font-size: 5rem;

  a {
      text-decoration: none;
      color: #4B5C77;
      font-size: 2rem;
      border: solid 0.2rem lightgray;

      :hover {
        background: rgb(205, 209, 206);
      }
  }
`;

const Title = styled.h1`
    text-align: center;
    width: 25%;
    margin-bottom: 2rem;
    border-bottom: solid 0.4rem #4B5C77;
`;

const Formfield = styled.div`
  position: relative;
  margin: 1.5rem;
  font-size: 5rem;
  
  input {
    appearance: none;
    border: none;
    border-bottom: 0.1rem solid #000;
    outline: none;
    transition: 300ms;

    :focus {
      border-bottom-color:#3498db;
    }
  }
`;

const Label = styled.label`
    color: #000;
    position: absolute;
    left: 0;
    bottom: 10%;
    transition: 300ms;

    ${({active}) => active && `font-size: 2rem; top: -1rem; color: gray;`};
`;

const Button = styled.input`
    margin: 3rem;
    padding: 0.5rem;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    border-radius: 1rem;
    border: none;
    background: #4B5C77;
    color: white;

    :hover {
        background: #525255;
    }

`;

const ErrorMessage = styled.p`
    color: red;
`;

const Login = () => {

  const [ list, setList ] = useState(new Set())
  const [loginfo, setLoginfo] = useState();
  const [errorinfo, setErrorinfo] = useState();

  function sendLoginfo(e) {
    e.preventDefault()
    fetch("/auth/login", {
      method: "POST",
      mode: "cors",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(loginfo),
    })
      .then((res) => res.json())
      .then((res) => {
        if (res === loginfo.username) {
          localStorage.setItem("user", res);
          window.location.href = "/";
        } else {
          setErrorinfo(res.message);
        }
      })
      .catch((error) => console.log(error));
  }

  function handleInput(e) {
    const { name, value } = e.target;
    setLoginfo((prevInfo) => {
        return { ...prevInfo, [name]: value };
    });
  }

  function addToList(e) {
    setList(prevInfo => new Set(prevInfo.add(e.target.name)))
  }

  function removeFromList(e) {
    if (!e.target.value) setList(prev => new Set([...prev].filter(x => x != e.target.name)))
  }

  return (
    <Loginform onSubmit={sendLoginfo}>
      <Title>Login</Title>
      <Formfield>
        <input 
          onChange={handleInput}
          onFocus={addToList} 
          onBlur={removeFromList}
          name="username" 
          id="username"
        />
        <Label htmlFor="username" active={list.has("username")}>Username:</Label>
      </Formfield>
      <Formfield>
        <input 
          onFocus={addToList} 
          onChange={handleInput}
          onBlur={removeFromList}
          name="password" 
          id="password"
        />
        <Label htmlFor="password" active={list.has("password")}>Password:</Label>
      </Formfield>
      <Button value="Login" type="submit" />
      <ErrorMessage>{errorinfo ? `${errorinfo}*` : ""}</ErrorMessage>
      <Link to="/register">Dont have an account? Register!</Link>
    </Loginform>
  );
};

export default Login;

