import { useState } from "react"
import styled from "styled-components"

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
`;

const Formstyled = styled.form`
    border-bottom: solid 0.2rem rgb(40, 40, 40);
    margin: 2rem;
    display: flex;
    flex-direction: column;
    font-size: 3rem;

    input {
        margin: 1rem;
        align-self: flex-start;
    }

    input[type=submit] {
        border: none;
        border-radius: 2rem;
        background-color:  #4B5C77;
        transition: background-color 0.1s ease-in-out;
        padding: 1rem;
        color: white;

        :hover {
            background-color: #525255;
        }
    }
`;  

const Buttons = styled.div`
    display: flex;
    flex-direction: column;
    font-size: 3rem;
    align-items: center;

    button {
        color: rgb(230, 244, 245);
        margin: 2rem;
        padding: 1rem;
        border: none;
        border-radius: 3rem;
        background-color:  #4B5C77;
        transition: background-color 0.1s ease-in-out;;

        :hover {
            background-color: #525255;
        }
    }
`;

const Usersettings = (props) => {

    const [ userdata, setUserdata ] = useState(props.location.state)

    function handleInput(e) {
        const { name, value }  = e.target

        setUserdata(prevInfo => { 
            return { ...prevInfo, [name]: value }
        })
    }

    function submitUserprofile() {
        fetch("/api/edit_user", {
            method: "PUT",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(userdata)
        })
    }

    function privateUser() {
        fetch("/api/private_user")
    }

    function unprivateUser() {
        fetch("/api/unprivate_user")
    }

    function deleteAccount() {
        fetch("/api/delete_account")
    }

    function logOut() {
        fetch("/auth/logout")
        localStorage.clear()
        window.location.href="/"
    }

    return (
        <Wrapper>
            {userdata &&
            <Wrapper>
            <Formstyled onSubmit={submitUserprofile}>
                <label>Bio:</label>
                <input type="text" value={userdata.bio} onChange={handleInput} name="bio"/>
                <label>Age:</label>
                <input type="text" value={userdata.age} onChange={handleInput} name="age"/>
                <label>Sex:</label>
                <input type="text" value={userdata.sex} onChange={handleInput} name="sex"/>
                <label>Avatar:</label>
                <input type="file" />
                <input type="submit" />
            </Formstyled>
            <Buttons>
                {userdata.private_profile ?
                <button onClick={unprivateUser}>Open Profile</button>
                :
                <button onClick={privateUser}>Private Profile</button>
                }   
                <button onClick={deleteAccount}>Delete Account</button>
                <button onClick={logOut}>Log Out</button>
            </Buttons>
            </Wrapper>
            }
        </Wrapper>
    )
}

export default Usersettings;
