import styled from "styled-components";

const Replymode = styled.div`
display: flex;
margin: 1rem;
margin-left: 2rem;
font-size: 2rem;
flex-direction: column;

    button {
        align-self: flex-end;
        margin: 0.5rem;
    }
`;

const Reply = (props) => {

    const { comment_id, createReply, handleInputReply, openReply } = props

    return (
        <Replymode>
            <textarea type="text" onChange={handleInputReply}/>
            <button name={comment_id} onClick={createReply}>Send Reply</button>
            <button onClick={openReply}>Cancel reply</button>
        </Replymode>
    )
}

export default Reply;
