import { useState, useEffect } from "react";
import { io } from "socket.io-client"
import styled from "styled-components";

const Wrapper = styled.div`
	font-size: 4rem;
`;

const Userlist = styled.div`
	font-size: 4rem;
	display: flex;
	flex-direction: column;
	flex: 1;
`;

const Chat = styled.div`
	display: flex;
	flex: 1;
	flex-direction: column;
`;

const Chatdiv = styled.div`
	display: flex;
  flex: 1;
`;

const Rightmess = styled.div`
	font-size: 3rem;
`;

const Leftmess = styled.div`
	font-size: 3rem;
`;

const Userdiv = styled.div``;

const Chatbox = () => {

  const loggedInUser = localStorage.getItem("user");

  const [useropen, setUserOpen] = useState(false);
  const [users, setUsers] = useState([]);
  const [messages, setMessages] = useState([]);
  const [messageinfo, setMessageinfo] = useState({ user:"", msg:"" });
  const { user, msg } = messageinfo;

  useEffect(() => {
    fetch("/api/display_follows")
      .then((response) => response.json())
      .then((data) => setUsers(data));
  }, []);

  function userChat(e) {

    const socket = io.connect("http://localhost:8080")

    socket.on("connect", () => {
        console.log("connected")
    })

    setUserOpen(true);

    const name = e.target.getAttribute("name");

    setMessageinfo(prevInfo => {
      return { ...prevInfo, user: name };
    });
  }

  function sendMessageinfo() {

  }

  function handleInput(e) {
    setMessageinfo((prevInfo) => {
      return { ...prevInfo, msg: e.target.value };
    });
  }

  function openChangeUser() {
    setMessages([]);
    setUserOpen(false);
  }

  return (
    <Wrapper>
      {useropen ? 

          <div>
            <button onClick={openChangeUser}>Back</button>

            {loggedInUser} you are in a chat with {user}

			      <Chat>
                  {messages && messages.map(message => {
                      return (
                        	<Chatdiv key={message._id}>
                        		{loggedInUser == message.sender ? 

                        		  <Rightmess>{message.msg}</Rightmess>
                            
                        		 : 
                            
                        		  <Leftmess>{message.msg}</Leftmess>
                            
                        		}
                        	</Chatdiv>
                      )
                  })}
			      </Chat>

            <input type="text" name="msg" value={msg} onChange={handleInput} />
            <button onClick={sendMessageinfo}>Send</button>
          </div>

         : 

          <Userlist>
            {users && users.map(user => {
                return (
                	<Userdiv
                	  key={user.id}
                	  className="user-link-chat"
                	  onClick={userChat}
                	  name={user.username}
                	>
            			{user.username}
                	</Userdiv>
                )
              })}
          </Userlist>
      }
    </Wrapper>
  )
};

export default Chatbox;
