import { useState } from "react"
import styled from "styled-components";

const Form = styled.form`
    display: flex;
    font-size: 3rem;
    flex-direction: column;
    margin: 2rem;

    h1 {
        font-size: 4rem;
        align-self: center;
        padding: 1rem;
    }

    textarea {
        height: 15rem;
    }

    input[type=text] {
        border: none;
        border-bottom: solid 0.2rem black;
        outline: none;
    }

    input[type=submit] {
        border: none;
        border-radius: 2rem;
        background-color: #4B5C77;
        transition: background-color 0.1s ease-in-out;
        padding: 1rem;
        align-self: flex-end;
        margin-right: 1rem;
        padding-left: 2rem;
        padding-right: 2rem;
        color: white;

        :hover {
            background-color: #525255;
        }
    }
`;

const Createpost = () => {
    const [ postinfo, setPostinfo ] = useState();

    function sendpostinfo(e) {
        e.preventDefault()
        fetch("/api/create_post", {
            method: "POST",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify(postinfo)
        })
        window.location.href = "/"
    }

    function handleInput(e) {
        const { name, value } = e.target
        setPostinfo(prevInfo => { return { ...prevInfo, [name]: value }})
    }

    function storeImage(e) {

        
    }
    
    return ( 
        <Form onSubmit={sendpostinfo} enctype="multipart/form-data">
            <h1>Create a new post</h1>
            Title:
            <input 
                type="text" 
                name="post_title" 
                onChange={handleInput}
            />
            Picture:
            <input 
                type="file" 
                name="picture"
                onChange={storeImage} 
                />
            Content:
            <textarea 
                placeholder="Be nice..."
                type="text" 
                name="post_content" 
                onChange={handleInput}
            />
            <br/>
            <input type="Submit" value="Submit Post" />
        </Form>
    )
}

export default Createpost;
