import Entrypage from "./Entrypage/Entrypage.js";
import Login from "./Auth/Login.js";
import Register from "./Auth/Register.js";
import NavbarOut from "./Navbar/NavbarOut.js";
import Footer from "./Footer/Footer.js";
import NotFound from "../Errorpage/NotFound.js";
import { Route, Switch } from "react-router-dom";
import styled from "styled-components"

const Wrapper = styled.div`
    min-height: 100vh;
    display: flex;
    flex-direction: column;
`;

const Main = styled.div`
    display: flex;
    background-image: linear-gradient(to right top, rgb(255, 255, 255), rgb(224, 224, 235));
    flex: 1;
`;

const LoggedOut = () => {

    return (
        <Wrapper>

            <NavbarOut />

          <Main>
          <Switch>
            <Route exact path="/"><Entrypage /></Route>
            <Route path="/login"><Login /></Route>
            <Route path="/register"><Register /></Route>
            <Route><NotFound /></Route>
          </Switch>
          </Main>

            <Footer />

        </Wrapper>
    );
};

export default LoggedOut;
