import { useState, useEffect } from "react"
import { Link } from "react-router-dom"
import styled from "styled-components";
import PostVote from "./PostPage/PostComponents/Vote"

const PostsDiv = styled.div`
    display: flex;
    margin: 1rem;
    border:  solid rgb(179, 175, 175) 0.1rem;
    border-bottom-right-radius: 2rem;
    text-decoration: none;
    color: black;

    :hover {
        background: lightgray;
    }
`;

const Content = styled.div`
    display: flex;
    text-align: start;
    flex-direction: column;
    padding-top: 1rem;
    padding-bottom: 1rem;
    align-items: flex-start;

    a {
        text-decoration: none;
        color: black;
        font-size: 1.5rem;
    }

    a:hover  {
        opacity: 0.5;
    }
`;

const Row = styled.div`
    display: flex;
    margin-top: auto;

    a {
        margin-left: 1rem;
    }
`;

const Title = styled(Link)`
    font-size: 2rem;
    word-break: break-all;
`;

const Number = styled.div`
    font-size: 1.5rem;
    margin-right: 1rem;
`;

const Noposts = styled.div`
    font-size: 3rem;
    margin: 2rem;
`;

const Post = () => {
    const [ posts, setPosts ] = useState();

    useEffect(() => {
        fetch("/api/display_posts")
        .then(response => response.json())
        .then(data =>  setPosts(data))
        .catch(error => console.log(error))

        function onScroll() {
            if (window.innerHeight + document.documentElement.scrollTop + 0.5 >= document.body.offsetHeight) {
                console.log("load data")
            }
        }
      
        window.addEventListener("scroll", onScroll);
      
        return function unMount() {
            window.removeEventListener("scroll", onScroll);
        };
    }, [])

    function votePost(e) {
        const post_id = e.target.getAttribute("id")
        let islike = e.target.getAttribute("name")

        fetch(`/api/vote_post`, {
            method: "PUT",
            mode: "cors",
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({ islike, post_id })    
        })

        renderVotechange(islike, post_id)
    }

    function renderVotechange(islike, post_id) {

        if (islike == "true") islike = true
        else islike = false

        posts.every(e => {
            if (e.id == post_id) {
                if (e.liked == islike) {
                    if (e.liked) e.post_votes -= 1
                    else e.post_votes += 1

                    e.liked = null
                }
                else {
                    if (e.liked == null && islike) e.post_votes += 1
                    else if (e.liked == null && !islike) e.post_votes -= 1
                    
                    if (e.liked != null && islike) e.post_votes += 2
                    else if (e.liked != null && !islike) e.post_votes -= 2

                    e.liked = islike
                }

                return false;
            }

            return true
        });
      
        setPosts([ ...posts ])
    }
   
    return ( 
        <> 
            {posts && posts.length ? posts.map(post => { 
                return (
                    <PostsDiv key={post.id}>
                        <PostVote 
                            votePost={votePost}
                            postinfo={post}
                        />
                        <Content>
                            <Title to={{ pathname: `/post/${post.id}` }}>
                                {post.post_title}
                            </Title>
                            <Row>
                                <Number>{post.num_of_comments} comment/s</Number>         
                                <Link to={{ pathname: `/userprofile/${post.username}` }}>     
                                    {post.username}      
                                </Link>
                            </Row>
                        </Content>                                       
                    </PostsDiv>
                )    
            }) 
            : 
            <Noposts>Create a post or follow someone too see some...</Noposts>}
        </>
    )
}

export default Post;
